import React from 'react'
import { Row } from 'react-bootstrap';
import WorkerItem from './CourseListItem'

export default function WorkerList(props) {
 return (
    <div>
       <Row>
        {props.course.map(course => {
          return <WorkerItem course={course}/>
        })
        }
      </Row>
    </div>
 )
}